.app {
    margin-top: 30px;
}
.navbar-inverse .navbar-brand,
.navbar-inverse .navbar-form,
.navbar-nav > li > a {
    padding-top: 0;
    padding-bottom: 0;
    margin: 0;
    line-height: 80px;
}
@media (max-width: 767px) {
    .navbar-nav > li > a {
        line-height: 20px;
        padding-top: 10px;
        padding-bottom: 10px;
    }
}
